<template>
	<footer>
		<div class="index-footer">
			<a href="#/"  v-bind:class="{ active: (getActive == 'index')}" v-on:click="changeFooterActive('index')">
				<span class="icon">			
					<i class="fa fa-home" aria-hidden="true"></i>
				</span>
				<span class="text">主页</span>
			</a>
			<a href="#/"  v-bind:class="{ active: (getActive== 'article')}" v-on:click="changeFooterActive('article')">
				<span class="icon">		
				<i class="fa fa-file-text" aria-hidden="true"></i>
				</span>
				<span class="text">文章</span>
			</a>
			<a href="#/question" v-bind:class="{ active: (getActive== 'question')}" v-on:click="changeFooterActive('question')">
				<span class="icon">		
				<i class="fa fa-question-circle-o" aria-hidden="true"></i>
				</span>
				<span class="text">问答</span>
			</a>
			<a href="#/my" v-bind:class="{ active: (getActive== 'my')}" v-on:click="changeFooterActive('my')">
				<span class="icon">		
				<i class="fa fa-user" aria-hidden="true"></i>
				</span>
				<span class="text">我的</span>
			</a>
		</div>
	</footer>
</template>
<script>
	import { mapGetters } from 'vuex'
	import { mapActions } from 'vuex'
	export default{
		name:'footer',
		computed: {
	      ...mapGetters([
	        'getActive'
	      ])
    	},
    	methods: {
	      ...mapActions([
	        'changeFooterActive'
	      ])
	    }
	}
</script>
<style scoped>
	.index-footer{
		position: fixed;
	    left: 0;
	    right: 0;
	    bottom: 0;
	    z-index: 9999;
	    display: -webkit-box;
	    display: -webkit-flex;
	    display: -ms-flexbox;
	    display: flex;
	    -webkit-box-align: center;
	    -webkit-align-items: center;
	    -ms-flex-align: center;
	    align-items: center;
	    -webkit-box-pack: justify;
	    -webkit-justify-content: space-between;
	    -ms-flex-pack: justify;
	    justify-content: space-between;
	    padding-top: .12rem;
	    background-color: #fff;    
	    -webkit-box-shadow: 0 -0.026667rem 0.053333rem rgba(0,0,0,.1);
	    box-shadow: 0 -0.026667rem 0.053333rem rgba(0,0,0,.1);
	}
	.index-footer a{
		-webkit-box-flex: 1;
	    -webkit-flex: 1;
	    -ms-flex: 1;
	    flex: 1;
	    text-align: center;
	}
	.index-footer a .icon{
		display: block;
		color: #666;
		font-size: 1.2rem;
		text-align: center;
		margin:0.2rem auto;
	}
	.index-footer a .text{
		display: block;
	    color: #666;
	    font-size: 0.6rem;
	}
	.index-footer a.active span{color: #ff3860}
</style>


