import Vue from 'vue'
import Vuex from 'vuex'

import * as actions from './actions'
import * as getters from './getters'

Vue.use(Vuex)

const state = {
	footerActive: 'index'
}

const mutations = {
	changeActive (state, amount) {
		state.footerActive = amount
	}
}

export default new Vuex.Store({
	actions,
	getters,
	state, 
	mutations
})



